import { Directive, HostListener,ElementRef, Renderer2, HostBinding} from '@angular/core';

@Directive({
  selector: '[cftHighlightMouse]'
})
export class HighlightMouseDirective {

  @HostListener('mouseenter') OnMouseOver(){
    // console.log("jjjjjjjjjjjjjjjj");
    // this._renderer.setStyle(this._elementRef.nativeElement,'background-color','yellow'); --solu√ß√£o 1
    this.backgroundColor = 'yellow';
  }

  @HostListener('mouseleave') OnMouseLeave(){
    // this._renderer.setStyle(this._elementRef.nativeElement,'background-color','white'); --solu√ß√£o 1
    this.backgroundColor = 'white';
  }

  // @HostBinding('style.backgroundColor') backgroundColor : string --solu√ß√£o 2

  @HostBinding('style.backgroundColor') get setColor(){
    return this.backgroundColor;
  }

  private backgroundColor: string; //solu√ß√£o 3

  // constructor(private _elementRef: ElementRef, --solu√ß√£o 1
  //             private _renderer : Renderer2) { } --solu√ß√£o 1

}
